import React from 'react';
import { connect } from 'react-redux';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { fetchNewsAnalyticsData } from '../../store/actions/dashboard.action';
import { DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

import {
  DataTableList,
  PTag,
  textFilter,
  dateFilter,
  CaptionElement,
} from '../../components/DataTable';
import { Link } from 'react-router-dom';
import moment from 'moment';
import { addDays } from 'date-fns';
import Input from 'react-select/src/components/Input';
import { newsget } from '../../store/actions/news.action';
import firebase from "firebase/app";
import { API } from '../../config/api.config';
import { PathApi } from '../../config/api.path.config';
import { successToast } from '../../lib/toasthelper';


interface IMainProps {
  loadData: (data: any) => void;
 
  loadCategory: () => void;
  dashboardData: any;
  getallnews: () => void;
  allnews: any;
}

class Index extends React.Component<IMainProps, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      //   daterange: [
      //     {
      //       startDate: new Date(),
      //       endDate: new Date(),
      //       key: 'selection',
      //     },
      //   ],
      fromDate: null,
      toDate: null,
    };
  }

  componentDidMount() {
     this.props.loadData({});

    console.log('this.props.dashboardData', this.props.dashboardData); 
    window.location.href="/dashboard/analytics/listnews";
  }

  doFilter = () => {
    this.props.loadData({
      fromDate: this.state.fromDate,
      toDate: this.state.toDate,
    });
  };
  handleSelect = (item: any) => {
    console.log('item', item);
    console.log('state', this.state.daterange);
    this.setState({ daterange: [item.selection] });
  };

  goback =() =>{
    window.location.href="/dashboard/analytics/news"
  }
 
  













  columns: any = [
    {
      formatter: (cell: any, row: any) => <PTag text={row.id} />,
      text: 'ID',
      dataField: 'id',
      sort: true,
      filter: false,
    },
    // {
    //   formatter: (cell: any, row: any) => (
    //     <a href={`https://tapyourchip.com/${row.id}`} target={'_blank'}>
    //       <PTag text={`https://tapyourchip.com/${row.id}`} />
    //     </a>
    //   ),
    //   text: 'Profile Link',
    //   dataField: 'id',
    //   sort: true,
    //   filter: false,
    // },
    {
      formatter: (cell: any, row: any) => <PTag text={row.title} />,
      text: 'Title',
      dataField: 'title',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.description} />,
      text: 'Description',
      dataField: 'description',
      sort: true,
      filter: textFilter(),
    
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.datetime} />,
      text: 'Date and Time',
      dataField: 'datetime',
      sort: true,
      filter: textFilter(),
   
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.longitude} />,
      text: 'Longitude',
      dataField: 'longitude',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.latitude} />,
      text: 'Latitude',
      dataField: 'latitude',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.location} />,
      text: 'Location',
      dataField: 'location',
      sort: true,
      filter: textFilter(),
      
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.generallocation} />,
      text: 'General Location',
      dataField: 'username',
      sort: true,
      filter: textFilter(),
      
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.statenews} />,
      text: 'State News',
      dataField: 'username',
      sort: true,
      filter: textFilter(),
      
    },
    {
      formatter: (cell: any, row: any) => (
        <Col>
          <Link
            to={{
              pathname: `/dashboard/newsDetails/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm">
              View
            </Button>
          </Link>
          <Link
            to={{
              pathname: `/dashboard/newsDetails/${row.id}/edit`,
            }}
          >
            <Button variant="warning" size="sm">
              Edit
            </Button>
          </Link>
      
          <Link
            to={{
              pathname: `/dashboard/newsDeletetest/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm"  style={{backgroundColor:"red"}}>
              Delete
            </Button>
          </Link>
        </Col>
      ),
      text: 'Action',
      dataField: 'id',
    },
   
    
    
  ];

  //   handleSubmit = (event: any) => {
  //     event.preventDefault();
  //     console.log(event.target.value);
  //   };

  render() {
    // const { dashboardData, } = this.props;

    const { dashboardData } = this.props;

    return (
      <Row>
        <Row>
          <CaptionElement title={'List of Alerts'} />
          <Button
            
            style={{backgroundColor:"red" , width:"fit-content", margin :"0 auto"}}
            onClick={() => this.goback()}
          >
           Add Alert
          </Button>
          
          {/* <DateRangePicker
            onChange={(item) => this.handleSelect(item)}
            ranges={this.state.daterange}
          /> */}
               
 
     {/*    { this.props.allnews && this.props.allnews.map((n:any)=><p key={n.id}>{n.title} {n.description}</p>)} */}










          {dashboardData.userAlanytics && (
            <DataTableList
              data={dashboardData.userAlanytics}
              columns={this.columns}
            />
          )}
        </Row>
      </Row>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  dashboardData: state.Dashboard,
  allnews: state.News.payload,
});
const mapDispatchToProps = (dispatch: any) => ({
  loadData: (data: any) => dispatch(fetchNewsAnalyticsData(data)),
  getallnews: () =>dispatch(newsget()),
});
export default connect(mapStateToProps, mapDispatchToProps)(Index);
