import React from 'react';
import { GiNautilusShell } from 'react-icons/all';
import unnamed from '../../../../../assets/images/MS.png';
const NavLogo = (props: any) => {
  let toggleClass = ['mobile-menu'];
  if (props.collapseMenu) {
    toggleClass = [...toggleClass, 'on'];
  }

  return (
    <>
      <div className="navbar-brand header-logo" style={{ justifyContent:"center"}}>
        <a href="" className="b-brand">
          <div className="b-bg text-dark" style={{marginTop:'160%'  }} >
            <img src={unnamed} style={styles}/>
           
          </div>
      {/*     <div style={{ background:"red",fontSize:15,marginLeft:"16%"}}> Metropolitan Security</div> */}
        </a>
      </div>
    
    </>
  );
};
const styles = {
  
      
  width: '150px',
  height:'100px',
       
    
}
export default NavLogo;
