import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { Row, Col } from 'react-bootstrap-v5';

const PageTitle = ({ title, subtitle, className, ...attrs }) => {
  const classes = classNames(
    ...className,
    'text-center',
    'text-md-left',
    'm-y-10',
  );

  return (
    <Col xs="12" sm="4" className={classes} {...attrs}>
      <h3 className="page-title text-uppercase d-inline">{title}</h3> <span className="page-subtitle">{subtitle}</span>
    </Col>
  );
};

PageTitle.propTypes = {
  /**
   * The page title.
   */
  title: PropTypes.string,
  /**
   * The page subtitle.
   */
  subtitle: PropTypes.string,
};

export default PageTitle;
