import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import OuterClick from './outerclick';
import NavLogo from './navlogo';
import NavContent from './navcontent';
import navigation from './menu-items';
import { NavConstant } from '../../../../store/actionTypes/nav.types.d';


interface IStateProps {
  layout: string;
  preLayout: string;
  collapseMenu: boolean;
  layoutType: string;
  navBackColor: string;
  navBackImage: string;
  navIconColor: string;
  navBrandColor: string;
  layout6Background: string;
  layout6BackSize: string;
  rtlLayout: string;
  navFixedLayout: string;
  boxLayout: boolean;
  navDropdownIcon: string;
  navListIcon: string;
  navActiveListColor: string;
  navListTitleColor: string;
  navListTitleHide: string;
  // onChangeLayout:any,
  windowWidth: number;
}

interface IDispatchProps {
  onToggleNavigation: () => void;
}

class SideBarNavigation extends React.Component<IStateProps & IDispatchProps> {
  resize = () => {
    const contentWidth = document.body.clientWidth ;
    // if (this.props.layout === 'horizontal' && contentWidth < 992) {
    //     this.props.onChangeLayout('vertical');
    // }
  };

  componentDidMount() {
    this.resize();
    window.addEventListener('resize', this.resize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resize);
  }

  render() {
    let navClass = ['pcoded-navbar'];

    if (
      this.props.preLayout !== null &&
      this.props.preLayout !== '' &&
      this.props.preLayout !== 'layout-6' &&
      this.props.preLayout !== 'layout-8'
    ) {
      navClass = [...navClass, this.props.preLayout];
    } else {
      navClass = [
        ...navClass,
        this.props.layoutType,
        this.props.navBackColor,
        this.props.navBrandColor,
        'drp-icon-' + this.props.navDropdownIcon,
        'menu-item-icon-' + this.props.navListIcon,
        this.props.navActiveListColor,
        this.props.navListTitleColor,
      ];

      if (this.props.layout === 'horizontal') {
        navClass = [...navClass, 'theme-horizontal'];
      }

      if (this.props.navBackImage) {
        navClass = [...navClass, this.props.navBackImage];
      }

      if (this.props.navIconColor) {
        navClass = [...navClass, 'icon-colored'];
      }

      if (!this.props.navFixedLayout) {
        navClass = [...navClass, 'menupos-static'];
      }

      if (this.props.navListTitleHide) {
        navClass = [...navClass, 'caption-hide'];
      }
    }

    if (this.props.windowWidth < 992 && this.props.collapseMenu) {
      navClass = [...navClass, 'mob-open'];
    } else if (this.props.collapseMenu) {
      navClass = [...navClass, 'navbar-collapsed'];
    }

    if (this.props.preLayout === 'layout-6') {
      document.body.classList.add('layout-6');
      document.body.style.backgroundImage = this.props.layout6Background;
      document.body.style.backgroundSize = this.props.layout6BackSize;
    }

    if (this.props.preLayout === 'layout-8') {
      document.body.classList.add('layout-8');
    }

    if (this.props.layoutType === 'dark') {
      document.body.classList.add('pnode-dark');
    } else {
      document.body.classList.remove('pnode-dark');
    }

    if (this.props.rtlLayout) {
      document.body.classList.add('pnode-rtl');
    } else {
      document.body.classList.remove('pnode-rtl');
    }

    if (this.props.boxLayout) {
      document.body.classList.add('container');
      document.body.classList.add('box-layout');
    } else {
      document.body.classList.remove('container');
      document.body.classList.remove('box-layout');
    }

    let navContent = (
      <div className="navbar-wrapper">
        <NavLogo
          collapseMenu={this.props.collapseMenu}
          windowWidth={this.props.windowWidth}
          onToggleNavigation={this.props.onToggleNavigation}
        />
        <NavContent navigation={navigation.items} />
       
      </div>
      
    );
    if (this.props.windowWidth < 992) {
      navContent = (
        <OuterClick>
          <div className="navbar-wrapper">
            <NavLogo
              collapseMenu={this.props.collapseMenu}
              windowWidth={this.props.windowWidth}
              onToggleNavigation={this.props.onToggleNavigation}
            />
            <NavContent navigation={navigation.items} />
          
          </div>


        </OuterClick>
        
      );
    }

    return (
      <>
        <nav className={navClass.join(' ')}>{navContent}</nav>
      </>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    layout: state.NavReducer.layout,
    preLayout: state.NavReducer.preLayout,
    collapseMenu: state.NavReducer.collapseMenu,
    layoutType: state.NavReducer.layoutType,
    navBackColor: state.NavReducer.navBackColor,
    navBackImage: state.NavReducer.navBackImage,
    navIconColor: state.NavReducer.navIconColor,
    navBrandColor: state.NavReducer.navBrandColor,
    layout6Background: state.NavReducer.layout6Background,
    layout6BackSize: state.NavReducer.layout6BackSize,
    rtlLayout: state.NavReducer.rtlLayout,
    navFixedLayout: state.NavReducer.navFixedLayout,
    boxLayout: state.NavReducer.boxLayout,
    navDropdownIcon: state.NavReducer.navDropdownIcon,
    navListIcon: state.NavReducer.navListIcon,
    navActiveListColor: state.NavReducer.navActiveListColor,
    navListTitleColor: state.NavReducer.navListTitleColor,
    navListTitleHide: state.NavReducer.navListTitleHide,
    windowWidth: document.body.clientWidth,
  };
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    onToggleNavigation: () => dispatch({ type: NavConstant.COLLAPSEM }),
    // onChangeLayout: (layout: any) => dispatch({type: NavConstant., layout: layout}),
  };
};
const styles = {
  width: '500%',
  height:'200%',
}
export default connect(mapStateToProps, mapDispatchToProps)(SideBarNavigation);
