import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PerfectScrollbar from 'react-perfect-scrollbar';
import NavGroup from './NavGroup';
import { NavConstant } from '../../../../../store/actionTypes/nav.types.d';
import unnamed from '../../../../../assets/images/white.png';

interface IStateProps {
  navigation: any;
  layout: string;
  loggedUser: string;
}

interface IDispatchProps {
  onNavContentLeave: () => void;
}

class NavContent extends Component<IStateProps & IDispatchProps> {
  state = {
    scrollWidth: 0,
    prevDisable: true,
    nextDisable: false,
  };

  scrollPrevHandler = () => {
    const wrapperWidth =
      document.getElementById('sidenav-wrapper')!.clientWidth;

    let scrollWidth = this.state.scrollWidth - wrapperWidth;
    if (scrollWidth < 0) {
      this.setState({ scrollWidth: 0, prevDisable: true, nextDisable: false });
    } else {
      this.setState({ scrollWidth: scrollWidth, prevDisable: false });
    }
  };

  scrollNextHandler = () => {
    const wrapperWidth =
      document.getElementById('sidenav-wrapper')!.clientWidth;
    const contentWidth =
      document.getElementById('sidenav-horizontal')!.clientWidth;

    let scrollWidth = this.state.scrollWidth + (wrapperWidth - 80);
    if (scrollWidth > contentWidth - wrapperWidth) {
      scrollWidth = contentWidth - wrapperWidth + 80;
      this.setState({
        scrollWidth: scrollWidth,
        prevDisable: false,
        nextDisable: true,
      });
    } else {
      this.setState({ scrollWidth: scrollWidth, prevDisable: false });
    }
  };

  render() {
   
    const navItems = this.props.navigation.map((item: any) => {
      if (this.props.loggedUser !== 'rammar@metropolitansecurity.com.lb') {
        switch (item.type) {
          case 'groupFullAccess':
            return (
              <NavGroup layout={this.props.layout} key={item.id} group={item} />
            );
          default:
            return false;
        }
      } else {
        switch (item.type) {
          case 'groupRestrictedAccess':
            return (
              <NavGroup layout={this.props.layout} key={item.id} group={item} />
            );
          default:
            return false;
        }
      }
    
    });

    let mainContent: any = '';
    if (this.props.layout === 'horizontal') {
      let prevClass = ['sidenav-horizontal-prev'];
      if (this.state.prevDisable) {
        prevClass = [...prevClass, 'disabled'];
      }
      let nextClass = ['sidenav-horizontal-next'];
      if (this.state.nextDisable) {
        nextClass = [...nextClass, 'disabled'];
      }

      mainContent = (
        <div className="navbar-content sidenav-horizontal" id="layout-sidenav">
          <a
            href="#!"
            className={prevClass.join(' ')}
            onClick={this.scrollPrevHandler}
            style={{background:"red"}}
          >
            <span />
          </a>
          <div id="sidenav-wrapper" className="sidenav-horizontal-wrapper">
            <ul
              id="sidenav-horizontal"
              className="nav pcoded-inner-navbar sidenav-inner"
              onMouseLeave={this.props.onNavContentLeave}
              style={{ marginLeft: '-' + this.state.scrollWidth + 'px' }}
            >
              {navItems}
            </ul>
          </div>
          <a
            href="#!"
            className={nextClass.join(' ')}
            onClick={this.scrollNextHandler}
          >
            <span />
          </a>
        </div>
      );
    } else {
      mainContent = (
        <div className="navbar-content pnode-scroll"  >
          <PerfectScrollbar>
            <ul className="nav pcoded-inner-navbar">{navItems}</ul>
            <div className="navbar-brand header-logo" style={{paddingLeft:'30%', top:'85%', position:'absolute', left:'0', bottom:'0'}}>
        <a href="https://digitalechoes.net/" target="_blank"className="b-brand">
          <div className="b-bg text-dark" >
            <img src={unnamed} style={styles}/>
          </div>
        </a>
      </div>
          </PerfectScrollbar>
        </div>
      );
    }

    return <>{mainContent}</>;
  }
}

const mapStateToProps = (state: any) => {
  return {
    layout: state.NavReducer.layout,
    collapseMenu: state.NavReducer.collapseMenu,
    loggedUser: state.AuthenticationReducer.email,
  };
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    onNavContentLeave: () => dispatch({ type: NavConstant.NAVCONTENTL }),
  };
};
const styles = {
  
  height:'150%',

 marginLeft:'146%',
 

}

export default connect(mapStateToProps, mapDispatchToProps)(NavContent);
