/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { Row, Col, Image, Table, Container } from 'react-bootstrap';
import { PathApi } from '../../../../config/api.path.config';
import { API } from '../../../../config/api.config';
import { successToast, warningToast } from '../../../../lib/toasthelper';
import RJSLoader from '../../../../components/RJSLoader';
import { Button } from 'react-bootstrap-v5';

class Index extends React.Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
      userData: null,
      directApp: null,
      isLoading: true,
    };
  }

  goback =() =>{
    window.location.href="/dashboard/analytics/listnews"
  }
   deleteRow = async () => {
    this.setState({ isLoading: true });
    
   await API.delete(PathApi.deleteNews  +this.props.match.params.newsId)
      .then((data: any) => {
if(data.status===200){  this.props.loadDataa();
  successToast('Deleted Successfully');
  this.setState({ isLoading: false });
 this.setIdList(this.props.match.params.newsId, false, 's');
 console.log(data);
}
      
      })
      .catch((error: any) => {
        this.setState({ isLoading: false });
       
      });
     window.location.href="/dashboard/analytics/listnews"
  };
  setIdList = (ids: any, isSelected: any, type: string = 's') => {
    if (type === 's') {
      let idArr = this.state.selectedIDs;
      if (!isSelected) {
        idArr = this.state.selectedIDs.filter((e: any) => e.id != ids.id);
      } else { 
        idArr.push(ids);
      }
      this.setState({ selectedIDs: idArr }, () => {
        console.log('ids s', this.state.selectedIDs);
      });
    } else {
      this.setState({ selectedIDs: isSelected ? ids : [] }, () => {
        console.log('ids m', this.state.selectedIDs);
      });
    }
  };
  componentDidMount() {
    console.log('***', this.props.match.params);
    console.log('testttt testt', this.props.match.params);
    this.getUserData();
  }

  getUserData = () => {
    API.get(PathApi.newsByid  + this.props.match.params.newsId) 
      .then((data: any) => {
        console.log('data', data);
        let userData = data.data;
        let directApp = null;
        if (userData?.isDirect) {
          directApp = userData[
            userData.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({ userData, isLoading: false, directApp });
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData: null, isLoading: true });
     
      });
  };

  render() {
    if (this.state.isLoading) {
      return <RJSLoader />;
    }
    const { userData, directApp } = this.state;
    return (
      <Container>
        <Col md="auto">

<div><p ><b>Are you sure you want to delete this news ?</b> </p></div>
<Button
           
            size="sm"
            style={{backgroundColor:"grey"}}
            onClick={() => this.deleteRow()}
          >
            Delete this news
          </Button>
          <Button
       
            size="sm"
            style={{backgroundColor:"green"}}
            onClick={() => this.goback()}
          >
           No, Go Back
          </Button>

          <div style={{ maxWidth:"300px" , justifyContent:"center" }}>
          <Row className="justify-content-md-center"  >
            <p >
              <span style={{ fontWeight: 'bold' }}>Title: </span>
              {userData?.title}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Description: </span>
              {userData?.description}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Date and Time: </span>
              {userData?.datetime}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p>
              <span style={{ fontWeight: 'bold' }}>Longitude: </span>
              {userData?.longitude}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Latitude: </span>
              {userData?.latitude}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Location: </span>
              {userData?.location}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>General Location: </span>
              {userData?.generallocation}
            </p>
          </Row>

          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>State news: </span>
              {userData?.statenews}
            </p>
          </Row>
          <br></br>
          </div>
        


        </Col>

      
        <Row style={{ height: 200 }}></Row>
      </Container>
    );
  }
}
export default Index;
