import React from 'react';
import { Route, Switch, Redirect } from 'react-router';
import { connect } from 'react-redux';
import Home from '../pages/dashboard/home';
import AddNewPost from '../pages/dashboard/post/AddNewPost';
import EditPost from '../pages/dashboard/post/EditPost';
import PostList from '../pages/dashboard/post/PostList';
import CategoryList from '../pages/dashboard/post/category/categorylist';
import ProductList from '../pages/dashboard/products/ProductList';
import ProductCategoryList from '../pages/dashboard/products/category/ProductCategoryList';
import EditProduct from '../pages/dashboard/products/EditProduct';
import AddNewProduct from '../pages/dashboard/products/AddNewProduct';
import Users from '../pages/dashboard/analytics/users';
import News from '../pages/dashboard/analytics/news';
import National from '../pages/dashboard/analytics/national';
import Tap from '../pages/dashboard/analytics/tap';
import SpecialNews from '../pages/dashboard/analytics/listspecialnews';
import UsersDetails from '../pages/dashboard/analytics/usersDetails';
import UsersDeletetest from '../pages/dashboard/analytics/usersDeletetest';
import NewsDeletetest from '../pages/dashboard/analytics/newsDeletetest';
import NationalnewsDeletetest from '../pages/dashboard/analytics/nationalnewsDeletetest';
import NewsDetails from '../pages/dashboard/analytics/newsDetails';
import NationalNewsDetails from '../pages/dashboard/analytics/nationalnewsDetails';
import SpecialNewsDetails from '../pages/dashboard/analytics/specialnewsDetails';
import UsersDetailsEdit from '../pages/dashboard/analytics/usersDetailsEdit';
import UsersDetailssEdit from '../pages/dashboard/analytics/usersDetailssEdit';
import NewsDetailsEdit from '../pages/dashboard/analytics/newsDetailsEdit';
import NationalNewsDetailsEdit from '../pages/dashboard/analytics/nationalnewsDetailsEdit';
import SpecialNewsDetailsEdit from '../pages/dashboard/analytics/specialnewsDetailsEdit';
import Listnews from '../pages/dashboard/analytics/listnews';
import NfcCreate from '../pages/dashboard/nfcstock/create';
import NfcList from '../pages/dashboard/nfcstock/list';
import EditNfc from '../pages/dashboard/nfcstock/EditNfc';
import Listnationalnews from '../pages/dashboard/analytics/listnationalnews';

import OrderList from '../pages/dashboard/orders/list';
import OrderDetails from '../pages/dashboard/orders/OrderDetails';

class AuthRouting extends React.Component<{ AuthenticationReducer?: any }> {
  render() {
    return (
      <>
        <Route exact path="/dashboard">
          <Home />
        </Route>
        <Route
          exact
          path="/dashboard/userDetails/:userId"
          render={(props) => <UsersDetails {...props} />}
        />
         <Route
          exact
          path="/dashboard/userDeletetest/:userId"
          render={(props) => <UsersDeletetest {...props} />}
        />
         <Route
          exact
          path="/dashboard/newsDeletetest/:newsId"
          render={(props) => <NewsDeletetest {...props} />}
        />
         <Route
          exact
          path="/dashboard/newsDetails/:newsId"
          render={(props) => <NewsDetails {...props} />}
        />
         <Route
          exact
          path="/dashboard/nationalnewsDetails/:nationalnewsId"
          render={(props) => <NationalNewsDetails {...props} />}
        />
         <Route
          exact
          path="/dashboard/nationalnewsDeletetest/:nationalnewsId"
          render={(props) => <NationalnewsDeletetest {...props} />}
        />
         <Route
          exact
          path="/dashboard/specialnewsDetails/:specialnewsId"
          render={(props) => <SpecialNewsDetails {...props} />}
        />
        <Route
          exact
          path="/dashboard/userDetails/:userId/edit"
          render={(props) => <UsersDetailsEdit {...props} />}
        />
        <Route
          exact
          path="/dashboard/userDetailss/:userId/edit"
          render={(props) => <UsersDetailssEdit {...props} />}
        />
         <Route
          exact
          path="/dashboard/newsDetails/:newsId/edit"
          render={(props) => <NewsDetailsEdit {...props} />}
        />
         <Route
          exact
          path="/dashboard/nationalnewsDetails/:nationalnewsId/edit"
          render={(props) => <NationalNewsDetailsEdit {...props} />}
        />
         <Route
          exact
          path="/dashboard/specialnewsDetails/:specialnewsId/edit"
          render={(props) => <SpecialNewsDetailsEdit {...props} />}
        />
        <Route exact path="/dashboard/analytics/users">
          <Users />
        </Route>
        <Route exact path="/dashboard/analytics/listnationalnews">
          <Listnationalnews />
        </Route>
        <Route exact path="/dashboard/analytics/listnews">
          <Listnews />
        </Route>
        <Route exact path="/dashboard/analytics/listspecialnews">
          <SpecialNews />
        </Route>
        <Route exact path="/dashboard/analytics/news">
          <News />
        </Route>
        <Route exact path="/dashboard/analytics/national">
          <National />
        </Route>
        <Route exact path="/dashboard/orders/list">
          <OrderList />
        </Route>
        <Route
          exact
          path="/dashboard/orders/:orderId/edit"
          render={(props) => <OrderDetails {...props} />}
        />
        <Route exact path="/dashboard/analytics/tap">
          <Tap />
        </Route>

        <Route exact path="/dashboard/nfcstock/create">
          <NfcCreate />
        </Route>
        <Route exact path="/dashboard/nfcstock/list">
          <NfcList />
        </Route>
        <Route exact path="/dashboard/nfcstock/edit/:id" component={EditNfc} />

        <Route exact path="/dashboard/post/list">
          <PostList />
        </Route>
        <Route exact path="/dashboard/post/categories">
          <CategoryList />
        </Route>
        <Route exact path="/dashboard/product/list">
          <ProductList />
        </Route>
        <Route exact path="/dashboard/product/categories">
          <ProductCategoryList />
        </Route>
        <Route exact path="/dashboard/post/add">
          <AddNewPost />
        </Route>
        <Route exact path="/dashboard/product/add">
          <AddNewProduct />
        </Route>
        <Route exact path="/dashboard/post/edit/:id" component={EditPost} />
        <Route
          exact
          path="/dashboard/product/edit/:id"
          component={EditProduct}
        />
      </>
    );
  }
}

export default connect((state) => state)(AuthRouting);
