/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { Row, Col, Image, Table, Container } from 'react-bootstrap';
import { PathApi } from '../../../../config/api.path.config';
import { API } from '../../../../config/api.config';
import { successToast, warningToast } from '../../../../lib/toasthelper';
import RJSLoader from '../../../../components/RJSLoader';
import { CaptionElement } from '../../../../components/DataTable';
import { Button } from 'react-bootstrap-v5';

class Index extends React.Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
      userData: null,
      directApp: null,
      isLoading: true,
    };
  }

  componentDidMount() {
    console.log('testtt testt1', this.props.match.params);
    console.log(this.props.match.params);
    this.getNewsData();
  }
  gobackk =() =>{
    window.location.href="/dashboard/analytics/listnationalnews"
  }

  getNewsData = () => {
    API.get(PathApi.nationalnewsByid + this.props.match.params.nationalnewsId)
      .then((data: any) => {
        console.log('data', data);
        let userData = data.data;
        let directApp = null;
        if (userData?.isDirect) {
          directApp = userData[
            userData.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({ userData, isLoading: false, directApp });
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData: null, isLoading: true });
        warningToast('Error in loading user data NFC data');
      });
  };

  render() {
    if (this.state.isLoading) {
      return <RJSLoader />;
    }
    const { userData, directApp } = this.state;
    return (
      <Container>
        <Col md="auto">
          <Row
            className="justify-content-md-center"
            style={{ paddingTop: 20, marginBottom: 10 }}
          >
            {/* {userData?.profilePic && ( */}
            {/* )} */}
          </Row>
          {/* </Col> */}
          <CaptionElement title={'Details of this National News-Metropolitan Security'} />
          <br></br>
          <div style={{ maxWidth:"300px" , justifyContent:"center" }}>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Title: </span>
              {userData?.title}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Description: </span>
              {userData?.description}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Risk Level: </span>
              {userData?.risklevel}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Longitude: </span>
              {userData?.longitude}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p>
              <span style={{ fontWeight: 'bold' }}>Latitude: </span>
              {userData?.latitude}
            </p>
          </Row>
          </div>
          <br></br>
          <Button
           
           variant="primary"
           className="shadow-2 mb-4"
           style={{width:"fit-content"}}
           onClick={() => this.gobackk()}
        
         >
           Go Back
         </Button>
         
         

      
        


        </Col>

      
        <Row style={{ height: 200 }}></Row>
      </Container>
    );
  }
}
export default Index;
