import { NATIONAL_NEWS_SET } from "../actionTypes/nationalnews.types.d";



const initialState = {

}

const nationalnewsreducer=(state = initialState,action:any)=>{
switch(action.type){

case NATIONAL_NEWS_SET :
    
         const { payload} =action;
             
            return {...state,payload }
 

default:
return state;
}}
export  default nationalnewsreducer

