import { API } from '../config/api.config';
import { PathApi } from '../config/api.path.config';

export function fetchUsersService() {
  return API.get(PathApi.getallusers)
    .then((data: any) => {
      return data;
    })
    .catch((error: any) => {
      return error.response;
    });
}

export function fetchTapsService(payload: any = {}) {
  console.log('fetchTapsService', payload);

  return API.post(PathApi.getallusers, payload.payload)
    .then((data: any) => {
      console.log('data', data);
      return data;
    })
    .catch((error: any) => {
      return error.response;
    });
}



