import { NATIONAL_NEWS_GET } from "../actionTypes/nationalnews.types.d";
import { NATIONAL_NEWS_SET } from "../actionTypes/nationalnews.types.d";

export function nationalnewsget (){
    const action = {type: NATIONAL_NEWS_GET}
    return action;
}

export function nationalnewsSet (payload :any){
    const action = {type: NATIONAL_NEWS_SET , payload}
    return action;
}