/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { Row, Col, Image, Table, Container } from 'react-bootstrap';
import { PathApi } from '../../../../config/api.path.config';
import { API } from '../../../../config/api.config';
import { successToast, warningToast } from '../../../../lib/toasthelper';
import RJSLoader from '../../../../components/RJSLoader';
import { Button } from 'react-bootstrap-v5';

class Index extends React.Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
      userData: null,
      userData1: null,
      directApp: null,
      isLoading: true,
    };
  }

  componentDidMount() {
    console.log('***', this.props.match.params);
    console.log('testttt testt', this.props.match.params);
    this.getUserData();
    this.getUserPasswordData();
   

  }
  gobackk =() =>{
    window.location.href="/dashboard/analytics/users"
  }
  getUserPasswordData = () => {
    API.get(PathApi.usersByidd  + this.props.match.params.userId) 
      .then((data: any) => {
        console.log('data', data);
        let userData1 = data.data;
        let directApp = null;
        if (userData1?.isDirect) {
          directApp = userData1[
            userData1.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData1.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({ userData1, isLoading: false, directApp });
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData1: null, isLoading: true });
     
      });
  };
  getUserData = () => {
    API.get(PathApi.usersByid  + this.props.match.params.userId) 
      .then((data: any) => {
        console.log('data', data);
        let userData = data.data;
        let directApp = null;
        if (userData?.isDirect) {
          directApp = userData[
            userData.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({ userData, isLoading: false, directApp });
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData: null, isLoading: true });
     
      });
  };
  render() {
    if (this.state.isLoading) {
      return <RJSLoader />;
    }
    const { userData,userData1, userData1directApp } = this.state;
    return (
      <Container>
        <Col md="auto">
          <Row
            className="justify-content-md-center"
            style={{ paddingTop: 20, marginBottom: 10 }}
          >
            {/* {userData?.profilePic && ( */}
            <Image
             src={"https://echoes.agency/files/"+userData?.email+".png"}
              thumbnail
              style={{ width: 200, height: 200 }}
            />
            {/* )} */}
          </Row>
          {/* </Col> */}
<br></br>
          <div style={{ maxWidth:"300px" ,textAlign:"center" , justifyContent:"center", margin:"0 auto"}}>
          <Row className="justify-content-md-center">
            <p>
              <span style={{ fontWeight: 'bold' }}>Username: </span>
              {userData?.username}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p>
              <span style={{ fontWeight: 'bold' }}>Password: </span>
              {userData1?.password}
            </p>
          </Row> 
          
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Email: </span>
              {userData?.email}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Phone Number: </span>
              {userData?.phonenumber}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Location: </span>
              {userData?.location}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Description: </span>
              {userData?.description}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Date of birth: </span>
              {userData?.dateofbirth}
            </p>
          </Row>
          <Row className="justify-content-md-center">
            <p >
              <span style={{ fontWeight: 'bold' }}>Type Account: </span>
              {userData?.typeaccount}
            </p>
          </Row>

      </div>
          <Button
           
           variant="primary"
           className="shadow-2 mb-4"
           style={{width:"fit-content"}}
           onClick={() => this.gobackk()}
        
         >
           Go Back
         </Button>


        </Col>
        

      
        <Row style={{ height: 200 }}></Row>
      </Container>
    );
  }
}
export default Index;
