import { call, put, takeLatest } from "redux-saga/effects";

import  GetNationalNews  from "../../services/nationalnews.service";

import { nationalnewsSet } from "../actions/nationalnews.action";

import { NATIONAL_NEWS_GET } from "../actionTypes/nationalnews.types.d";

export function* handleGetNationalNews() :any {

  try {

    const response = yield call(GetNationalNews);

    if (response.status === 200) {

      const { data } = response;

      console.log("product data", data);

      yield put(nationalnewsSet(data));

    } else {

      throw new Error("Could not connect to API");

    }

  } catch (err) {

    console.log(err);

  }

}

export function* watchnationalnewssaga() {

    yield takeLatest(NATIONAL_NEWS_GET, handleGetNationalNews);
  
  }

