/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { Row, Col, Image, Table, Container } from 'react-bootstrap';
import { PathApi } from '../../../../config/api.path.config';
import { API } from '../../../../config/api.config';
import {
  successToast,
  warningToast,
  errorToast,
} from '../../../../lib/toasthelper';
import RJSLoader from '../../../../components/RJSLoader';
import { Button } from 'react-bootstrap-v5';
import axios from 'axios';
import { gettype } from '../../../../utils/typeaccounthelper';
let token = localStorage.getItem('token');
class Index extends React.Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
    
      uploadu : {
     
      username: null,
      email:null,
      phonenumber:null,
      location:null,
      description:null,
      dateofbirth:null,
      typeaccount:null,
      password:null,
      validemail:'',
      disabled:false,
      },
      uploadFile : null,
      userData: null,
      userData1: null,
      directApp: null,
      isLoading: true,
    };
  }

  componentDidMount() {
    console.log('***', this.props.match.params);
    this.getUserData();
    this.getUserPasswordData();
  }
  gobackk =() =>{
    window.location.href="/dashboard/analytics/users"
  }
  getUserPasswordData = () => {
    API.get(PathApi.usersByidd + this.props.match.params.userId)
      .then((data: any) => {
        console.log('data', data);
        let userData1 = data.data;
        let directApp = null;
        if (userData1?.isDirect) {
          directApp = userData1[
            userData1.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData1.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({
          userData1,
          isLoading: false,
          directApp,
        });
        this.setState({uploadu : {...this.state.uploadu, password:userData1.password}});
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData1: null, isLoading: true });
        warningToast('Error in loading user data NFC data');
      });
  };

  uploadImage = async () => {
    this.setState({ isLoading: true });
    const formData = new FormData();
  formData.append("selectedFile", this.state.uploadFile,this.state.uploadu.email+".png");
    try {
      const response = await axios({
        method: "post",
        url: "https://echoes.agency/files",
        data: formData,
        headers: { "Content-Type": "multipart/form-data"/* ,Authorization: token ? 'Bearer ' +  token : '', */ },
      });
      if (response.status === 200) {
        this.getUserData();
        successToast('Sucess in adding imagee');
      } else {
       errorToast("error in addingg image");
      }
    } catch (error: any) {
      console.log('error', error);
        this.setState({ isLoading: false });
      errorToast("error in adding imagee");
    }
    /* window.location.href="/dashboard/analytics/users"; */
  };

  getUserData = () => {
    API.get(PathApi.usersByid + this.props.match.params.userId)
      .then((data: any) => {
        console.log('data', data);
        let userData = data.data;
        let directApp = null;
        if (userData?.isDirect) {
          directApp = userData[
            userData.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({
          userData,
          isLoading: false,
          directApp,       
        });
        this.setState({uploadu : {...this.state.uploadu,  username: userData.username,
          email:userData.email,
          phonenumber:userData.phonenumber,
          location:userData.location,
          description:userData.description,
          dateofbirth:userData.dateofbirth,
          typeaccount:userData.typeaccount,}});
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData: null, isLoading: true });
        warningToast('Error in loading user data NFC data');
      });
  };

  updateUserData = () => {
    this.setState({ isLoading: true });
    
    const postdata = {
      uploadu : { username: this.state.uploadu.username,
     email: this.state.uploadu. email,
       phonenumber: this.state.uploadu.phonenumber,
       location: this.state.uploadu.location,
       description: this.state.uploadu.description,
       dateofbirth: this.state.uploadu.dateofbirth,
        typeaccount: this.state.uploadu.typeaccount}
  };

    /* username : this.state.uploadu.username,
      email: this.state.uploadu.email,
      phonenumber: this.state.uploadu.phonenumber,
      location: this.state.uploadu.location,
      description: this.state.uploadud.escription,
      dateofbirth: this.state.uploadu.dateofbirth,
      typeaccount: this.state.uploadu.typeaccount, */
     
    
    API.patch(PathApi.userUpdateById + this.props.match.params.userId, postdata.uploadu)
      .then((data: any) => {
        console.log('data', data);
        successToast('User data Updated');
        this.getUserData();
      })
      .catch((error: any) => {
        console.log('error', error);
        this.setState({ isLoading: false });
        errorToast('Error in updating user data');
      });
      if (this.state.uploadFile !== null){
        this.uploadImage();
        }
  };

  updateUserPasswordData = () => {
    this.setState({ isLoading: true });

    const postdata1 = {
      uploadu : {
      password: this.state.uploadu.password,
      }
     
    };
    API.patch(PathApi.userUpdateByIdd + this.props.match.params.userId, postdata1.uploadu)
      .then((data: any) => {
        console.log('data', data);
        successToast('User data Updated');
        this.getUserData();
      })
      .catch((error: any) => {
        console.log('error', error);
        this.setState({ isLoading: false });
        errorToast('Error in updating user data');
      });
  };




  async checkvalue (inputemail:any)  {

    const res =   await axios.get("https://echoes.agency/user", { headers: {
      'Access-Control-Allow-Origin': '*',
      Authorization: token ? 'Bearer ' +  token : '',
    }});
    console.log("hiiii")
    console.log('dddddddddddddddddddddddd',res.data)
    console.log('fffffffffffffff',inputemail)
    console.log("okayy", res.status)
    if(res.status==200){
      console.log("fhemnnaa", res.status)
    }
    for (var i = 0; i < res.data.length; i++){
     
           if(res.data[i].email == inputemail){
            console.log(res.data[i].email);
            console.log(inputemail);
            this.setState({uploadu: {...this.state.uploadu, validemail : "the user already exists"}});
            this.setState({uploadu: {...this.state.uploadu,disabled:true}});
            return;
           }
        
    }
    this.setState({uploadu: {...this.state.uploadu, validemail : ""}})
    this.setState({uploadu: {...this.state.uploadu, disabled:false}})
  }
  render() {
    if (this.state.isLoading) {
      return <RJSLoader />;
    }
    const { userData, userData1} = this.state;
    return (

<form className="row g-3"  onSubmit={ () => {
  
  
  this.updateUserData();
  this.updateUserPasswordData();

  
  
  
}

}
>
      <Container>
        <Col md="auto">
      
          <Row
            className="justify-content-md-center"
            style={{ paddingTop: 20, marginBottom: 10 }}
          >
            <Image
              src={"https://echoes.agency/files/"+userData?.email+".png"}
              thumbnail
              style={{ width: 200, height: 200 }}
            />
          </Row>
          <div className="form-group mb-4 col-4">
                  <label htmlFor="exampleInputEmail1">Edit Profile Picture</label>

                  <input
                    className="form-control"
                
                    aria-describedby="dateHelp"
                    placeholder="Profile Picture"
                    type="file"
                    onChange={(e) => {
                      this.setState({ uploadFile: e.target.files?.[0]});
                    }}
                  />
                </div>
            <div className="col-md-6">
              <label htmlFor="inputEmail4" className="form-label">
                Username
              </label>
              <input
                placeholder="Name"
                required
                className="form-control"
                value={this.state.uploadu.username}
                onChange={(e) =>{ this.setState({ uploadu : {...this.state.uploadu ,username: e.target.value} });
              console.log("hiiiiiiii",this.state.uploadu.username)}}
              
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputEmail4" className="form-label">
                Password
              </label>
              <input
                placeholder="Password"
                required
                className="form-control"
                minLength={8}
                value={this.state.uploadu.password}
                onChange={(e) => this.setState({ uploadu : {...this.state.uploadu ,password: e.target.value} })}
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputEmail4" className="form-label">
                Email
              </label>
              <input
              disabled
                placeholder="Email"
                type="email"
                className="form-control"
                value={this.state.uploadu.email}
                onChange={(e) => this.setState({ uploadu : {...this.state.uploadu ,email: e.target.value }})}
                onKeyUp={() => this.checkvalue(this.state.uploadu.email)}
              />
                  <p style={{color:"red"}}>  {this.state.uploadu.validemail}</p> 
            </div>
        

            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputEmail4" className="form-label">
                Phone number
              </label>
              <input
             
                placeholder="Phone number"
                type="text"
                className="form-control"
                value={this.state.uploadu.phonenumber}
                onChange={(e) => this.setState({uploadu : {...this.state.uploadu , phonenumber: e.target.value }})}
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
                Location
              </label>
              <input
         
                type="text"
                className="form-control"
                placeholder="Location"
                value={this.state.uploadu.location}
                onChange={(e) => this.setState({uploadu : {...this.state.uploadu , location: e.target.value}})}
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
                Description
              </label>
              <textarea
               rows={10} cols={50}
          
              
                className="form-control"
                placeholder="Description"
                value={this.state.uploadu.description}
                onChange={(e) => this.setState({ uploadu : {...this.state.uploadu ,description: e.target.value} })}
               
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
                Date of birth
              </label>
              <input
      
                type ="date"
                className="form-control"
                placeholder="Date of birth"
                value={this.state.uploadu.dateofbirth}
                onChange={(e) => this.setState({uploadu : {...this.state.uploadu , dateofbirth: e.target.value}})}
              
              />
            </div>
            <br></br>
            <div className="  mb-4 col-4">
                <label htmlFor="exampleInputEmail1">Type Account :</label>
                <br></br>
                <select
              
               
                  id="exampleFormControlSelect1"
                  aria-label="Default select example"
                  onChange={(e: any) => {
                    this.setState({uploadu: {...this.state.uploadu, typeaccount: e.target.value} })
                  }}
                >
                  <option  hidden value={''}>{this.state.uploadu.typeaccount}</option>
                  {gettype().map((e: string, i: any) => (
                    <option key={i} value={e}>
                      {e}
                    </option>
                  ))}
                </select>
              </div>
            <br></br>
           
            <div className="col-12">
              <button
                type="submit"
                className="btn btn-primary"
                disabled={this.state.uploadu.disabled}
            //    onClick={() => this.updateUserData()}
              >
                Update
              </button>
              <Button
           
           variant="primary"
       
           style={{width:"fit-content"}}
           onClick={() => this.gobackk()}
        
         >
           Go Back
         </Button> 
            </div>
         
        </Col>
        <Row style={{ height: 200 }}></Row>
      </Container>
      </form>
    );
  }
}
export default Index;
