import { call, put, takeLatest } from "redux-saga/effects";

import  GetNews  from "../../services/news.service";

import { newsSet } from "../actions/news.action";

import { NEWS_GET } from "../actionTypes/news.types.d";

export function* handleGetNews() :any {

  try {

    const response = yield call(GetNews);

    if (response.status === 200) {

      const { data } = response;

      console.log("product data", data);

      yield put(newsSet(data));

    } else {

      throw new Error("Could not connect to API");

    }

  } catch (err) {

    console.log(err);

  }

}

export function* watchnewssaga() {

    yield takeLatest(NEWS_GET, handleGetNews);
  
  }

