import React from 'react';
import { connect } from 'react-redux';
import { Row, Col, Card, Table, Button, Dropdown, Form } from 'react-bootstrap';
import { fetchTapCountData } from '../../../../store/actions/dashboard.action';
// import DataTable, { defaultThemes } from 'react-data-table-component';
import { CaptionElement, DataTableList, PTag } from '../../../../components/DataTable';
import { countryList, getCountry } from '../../../../utils/CountryHelpers';
import { getrisk } from '../../../../utils/risklevelhelper';
import { API } from '../../../../config/api.config';
import { PathApi } from '../../../../config/api.path.config';
import axios from 'axios';
import { errorToast, successToast } from '../../../../lib/toasthelper';
let token = localStorage.getItem('token');

interface IMainProps {
  loadData: (data: any) => void;
  loadCategory: () => void;
  dashboardData: any;
  tapCount: any;
}

class Index extends React.Component<IMainProps, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      title: '',
      description: '',
      risklevel: '',
      longitude: '',
      latitude:'',
     
     
     
    };
  }
  componentDidMount() {
    this.props.loadData({});
  }
  gohome =() =>{
    window.location.href="/dashboard/analytics/listnews"
  }
  alertfu =() =>{
    alert("Done!")
  }
  columns: any = [
    {
      cell: (row: any) => <PTag text={row.id} />,
      name: 'ID',
      sortable: true,
    },
    {
      cell: (row: any) => (
        <a href={`https://tapyourchip.com/${row.id}`} target={'_blank'}>
          <PTag text={`https://tapyourchip.com/${row.id}`} />
        </a>
      ),
      name: 'Profile Link',
      sortable: true,
    },
    {
      cell: (row: any) => <PTag text={row.firstName} />,
      name: 'First Name',
      sortable: true,
    },
    {
      cell: (row: any) => <PTag text={row.lastName} />,
      name: 'Last Name',
      sortable: true,
    },
    {
      cell: (row: any) => <PTag text={row.username} />,
      name: 'Username',
      sortable: true,
    },
    {
      cell: (row: any) => <PTag text={row.email} />,
      name: 'Email',
      sortable: true,
    },
    {
      cell: (row: any) => <PTag text={row.countryCode} />,
      name: 'Country Code',
      sortable: true,
    },
    {
      cell: (row: any) => (
        <PTag text={(row?.callingCode ?? '') + ' ' + (row.mobile ?? '')} />
      ),
      name: 'Mobile',
      sortable: true,
    },
  ];
addNationalNews = async () => {
/* await axios.post("https://still-chamber-01106.herokuapp.com/http://142.93.101.79/nationalrisknews",this.state, { headers: {
    'Access-Control-Allow-Origin': '*',
    Authorization: token ? 'Bearer ' +  token : '',
  }}) */
// window.location.href ='/dashboard/analytics/national';



try {
 const response= await axios.post("https://echoes.agency/nationalrisknews",this.state, { headers: {
    'Access-Control-Allow-Origin': '*',
    Authorization: token ? 'Bearer ' +  token : '',
  }});
  console.log('response', response);

  if (response.status === 200) {
   
    successToast('Success');
  } else {
   errorToast("error");
  }
} catch (error: any) {
  errorToast("error");
}


}
  doFilter = () => {
    this.props.loadData({
      fromDate: this.state.fromDate,
      toDate: this.state.toDate,
      country: this.state.country,
      city: this.state.city,
    });
  };
  getCity = (country: any) => {
    console.log('this', country);

    this.setState({ country, city: null });
    API.get(PathApi.citylistsByCountry + country)
      .then((data: any) => {
        console.log(data);
        this.setState({ cityList: data.data });
      })
      .catch((error: any) => {
        console.log(error);
        //  error.response;
      });
    // this.props.loadData({
    //   fromDate: this.state.fromDate,
    //   toDate: this.state.toDate,
    //   country: this.state.country,
    //   city: this.state.city,
    // });
  };

  render() {
    const { dashboardData } = this.props;
    return (
      <Row>
        <Row>
        <CaptionElement title={'Add National News-Metropolitan Security'} />
        <Button
           
           variant="primary"
           className="shadow-2 mb-4"
           style={{width:"fit-content"}}
           onClick={() => this.gohome()}
        
         >
           Go to Homepage
         </Button>
        <form    onSubmit={async () => {
             this.addNationalNews();
                 this.alertfu();
              }}>
          <div>
            <div style={{ flexDirection: 'row' }}>
              <div className="form-group mb-4 col-4">
                <label htmlFor="exampleInputEmail1" >Title (Required) </label>
                <input
                  required
                  className="form-control"
                 
                  aria-describedby="dateHelp"
                  placeholder="Title"
                  onChange={(e) => this.setState({ title: e.target.value })}
                  
                />
              </div>
             
              <div className="form-group mb-4 col-4">
                <label htmlFor="exampleInputEmail1">Description</label>

                <textarea
               rows={10} cols={50}
                  className="form-control"
               
                  aria-describedby="dateHelp"
                  placeholder="Description of the national risk news"
                  onChange={(e) => this.setState({ description: e.target.value })}
                />
              </div>
             
             
            
              
              
            
              
             



              <div className="form-group mb-4 col-4">
                <label htmlFor="exampleInputEmail1" >Longitude</label>
                <input
                
                  className="form-control"
                 required
                  aria-describedby="dateHelp"
                  placeholder="Longitude"
                  onChange={(e) => this.setState({ longitude: e.target.value })}
                  
                />
              </div>
              <div className="form-group mb-4 col-4">
                <label htmlFor="exampleInputEmail1" >Latitude</label>
                <input
            required
                  className="form-control"
                 
                  aria-describedby="dateHelp"
                  placeholder="Latitude"
                  onChange={(e) => this.setState({ latitude: e.target.value })}
                  
                />
              </div>
              <div className=" dropdown mb-4 col-4">
                <label htmlFor="exampleInputEmail1">Select Risk Level : </label>
                <br></br>
                <select
               
                required
                  id="exampleFormControlSelect1"
                  aria-label="Default select example"
                  onChange={(e: any) => {
                    this.setState({ risklevel: e.target.value })
                  }}
                >
                  <option value={''}>Select Risk Level</option>
                  {getrisk().map((e: string, i: any) => (
                    <option key={i} value={e}>
                      {e}
                    </option>
                  ))}
                </select>
              </div>
             
            </div>
            <Button
           type="submit"
              variant="primary"
              className="shadow-2 mb-4"
          
           
            >
              Add National News 
            </Button>
          </div>
          
          </form>
          {/* <Card.Header>
            <Row>
              <Col> Add New Category</Col>
              <Col className="d-flex justify-content-end"></Col>
            </Row>
          </Card.Header>
          <Card.Body>
            <p>jdfnjnf</p>
          </Card.Body> */}
                     
   
        </Row>
      </Row>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  dashboardData: state.Dashboard,
});
const mapDispatchToProps = (dispatch: any) => ({
  loadData: (data: any) => dispatch(fetchTapCountData(data)),
});
export default connect(mapStateToProps, mapDispatchToProps)(Index);
