import React from 'react';
import PostForm from './PostForm';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { isEmpty } from 'lodash';
import { sanitizeNU } from '../../../utils/stringHelpers';
import { postRequest } from '../../../store/actions/post.action';

interface IEditProps {
  initialValue: any;
  postRequest: any;
}

class EditPost extends React.Component<IEditProps, { postRequest: any }> {
  handleSubmit = (values: any) => {
    return this.props.postRequest(sanitizeNU(values));
  };

  render() {
    const { initialValue } = this.props;
    return (
      <>
        {(!isEmpty(initialValue) && (
          <PostForm
            onSubmit={this.handleSubmit}
            initialValues={{
              post: {
                id: initialValue?.id,
                title: initialValue?.title,
                slug: initialValue?.slug,
                bannerImage:
                  initialValue?.bannerImage === null
                    ? undefined
                    : initialValue?.bannerImage,
                description: initialValue?.description,
              },
              postCategories:
                (initialValue?.hasOwnProperty('categories') &&
                  initialValue.categories.map((item: any) => item.id)) ||
                [],
            }}
            {...{ editMode: true }}
          />
        )) || <Redirect to="/dashboard/post/list" />}
      </>
    );
  }
}
const mapStateToProps = (state: any, ownProps: any) => {
  return {
    initialValue: ownProps.location.state.post,
  };
};
export default connect(mapStateToProps, { postRequest })(EditPost);
