/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { Row, Col, Image, Table, Container } from 'react-bootstrap';
import { PathApi } from '../../../../config/api.path.config';
import { API } from '../../../../config/api.config';
import {
  successToast,
  warningToast,
  errorToast,
} from '../../../../lib/toasthelper';
import RJSLoader from '../../../../components/RJSLoader';
import { CaptionElement } from '../../../../components/DataTable';
import { getCountryy } from '../../../../utils/statenewshelper';
import { getCountry } from '../../../../utils/CountryHelpers';
import { getGeneralLocation } from '../../../../utils/generallocationhelper';
import { Button } from 'react-bootstrap-v5';

class Index extends React.Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
      userData: null,
      directApp: null,
      isLoading: true,
      title: null,
      description:null,
      datetime:null,
      longitude:null,
      latitude:null,
      location:null,
      generallocation:null,
      statenews:null,
  
     
    };
  }

  componentDidMount() {
    console.log('***', this.props.match.params);
    this.getUserData();
  }
  gobackk =() =>{
    window.location.href="/dashboard/analytics/listnews"
  }

  getUserData = () => {
    API.get(PathApi.newsByid + this.props.match.params.newsId)
      .then((data: any) => {
        console.log('data', data);
        let userData = data.data;
        let directApp = null;
        if (userData?.isDirect) {
          directApp = userData[
            userData.profileTabType == 'Personal'
              ? 'socialProfiles'
              : 'businessProfiles'
          ].filter((e: any) => e.id == userData.activeDirect)[0];
        }
        // successToast('User data loaded');
        this.setState({
          userData,
          isLoading: false,
          directApp,
          title: userData.title,
          description:userData. description,
          datetime:userData. datetime,
          longitude:userData.longitude,
          latitude:userData.latitude,
          location:userData.location,
          generallocation:userData.generallocation,
          statenews:userData.statenews,
        
        });
      })
      .catch((error: any) => {
        // console.log(error);
        this.setState({ userData: null, isLoading: true });
        warningToast('Error in loading user data NFC data');
      });
  };

  updateUserData = () => {
    this.setState({ isLoading: true });

    const postdata = {
      title: this.state.title,
      description: this.state.description,
      datetime: this.state.datetime,
      longitude: this.state.longitude,
      latitude: this.state.latitude,
      location: this.state. location,
      generallocation: this.state.generallocation,
      statenews:this.state.statenews,
     
    };
    API.patch(PathApi.newsUpdateById + this.props.match.params.newsId, postdata)
      .then((data: any) => {
        console.log('data', data);
        successToast('User data Updated');
        this.getUserData();
      })
      .catch((error: any) => {
        console.log('error', error);
        this.setState({ isLoading: false });
        errorToast('Error in updating user data');
      });
  };

  render() {
    if (this.state.isLoading) {
      return <RJSLoader />;
    }
    const { userData } = this.state;
    return (
      <form className="row g-3"  onSubmit={this.updateUserData}>
      <Container>
        <Col md="auto">
          <Row
            className="justify-content-md-center"
            style={{ paddingTop: 20, marginBottom: 10 }}
          >
              <CaptionElement title={'Edit News-Metropolitan Security'} />
          </Row>
         
            <div className="col-md-6">
              <label  className="form-label">
                Title
              </label>
              <input
              required
                placeholder="Title"
                type="text"
                className="form-control"
                value={this.state.title}
                onChange={(e) => this.setState({ title: e.target.value })}
              />
            </div>
            <br></br>
            
            <div className="col-md-6">
              <label >
                Description
              </label>
              <br></br>
              <textarea
               rows={10} cols={50}
                placeholder="Description"
             
              
                value={this.state.description}
                onChange={(e) => this.setState({ description: e.target.value })}
           
              />
            </div>
            <br></br>
              <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
                Longitude
              </label>
              <input
           
                type="text"
                className="form-control"
                placeholder="Longitude"
                value={this.state.longitude}
                onChange={(e) => this.setState({ longitude: e.target.value })}
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
           Latitude
              </label>
              <input
           
                type="text"
                className="form-control"
                placeholder="latitide"
                value={this.state.latitude}
                onChange={(e) => this.setState({ latitude: e.target.value })}
               
              />
            </div>
            <br></br>
            <div className="col-md-6">
              <label htmlFor="inputAddress" className="form-label">
             Location
              </label>
              <input
            
                type="text"
                className="form-control"
                placeholder="Location"
                value={this.state.location}
                onChange={(e) => this.setState({ location: e.target.value })}
              
              />
            </div>
            <br></br>
            <div className="form-group mb-4 col-4">
                <label >Date And Time : </label>
               
                <input
           
                  className="form-control"
                  
                
                  placeholder="Date and Time"
                  type="datetime-local"
                  value={this.state.datetime}
                  onChange={(e) => this.setState({ datetime: e.target.value })}
                />
              </div>
            <div className=" dropdown mb-4 col-4">
                <label htmlFor="exampleInputEmail1">General Location : </label>
                <br></br>
           
                <select
             
             
                  id="exampleFormControlSelect1"
                  aria-label="Default select example"
                
                  onChange={(e: any) => {
                    
                    this.setState({ generallocation: e.target.value })
                  }}
                
                >
                  <option hidden value={''}>{this.state.generallocation}</option>
                  {getGeneralLocation().map((e: string, i: any) => (
                    <option key={i} value={e}>
                      {e}
                     
                    </option>
                    
                  ))}
                </select>
              </div>
              <br></br>
            <div className=" dropdown mb-4 col-4">
                <label htmlFor="exampleInputEmail1">State News : </label>
                <br></br>
                <select
             
              
                  id="exampleFormControlSelect1"
                  aria-label="Default select example"
                  onChange={(e: any) => {
                    this.setState({ statenews: e.target.value })
                  }}
                >
                  <option hidden value={''}>{this.state.statenews}</option>
                  {getCountryy().map((e: string, i: any) => (
                    <option key={i} value={e}>
                      {e}
                    </option>
                  ))}
                </select>
              </div>
              <br></br>


         
           
            <div className="col-12">
              <button
                type="submit"
                className="btn btn-primary"
           //     onClick={() => this.updateUserData()}
              >
                Update
              </button>
              <Button
           
           variant="primary"
          
           style={{width:"fit-content"}}
      onClick={() => this.gobackk()}
        
         >
           Go Back
         </Button>
            </div>
          
        </Col>
        <Row style={{ height: 200 }}></Row>

      </Container>
      </form>
    );
  }
}
export default Index;
