import React from 'react';
import { connect } from 'react-redux';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { fetchUserAnalyticsData } from '../../../../store/actions/dashboard.action';
import { DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

import {
  DataTableList,
  PTag,
  textFilter,
  dateFilter,
  CaptionElement,
} from '../../../../components/DataTable';
import { Link } from 'react-router-dom';
import moment from 'moment';
import { addDays } from 'date-fns';
import Input from 'react-select/src/components/Input';
import { usersget } from '../../../../store/actions/users.action';
import { API } from '../../../../config/api.config';
import { PathApi } from '../../../../config/api.path.config';
import { successToast } from '../../../../lib/toasthelper';
import { confirmAlert } from 'react-confirm-alert';
import { Modal } from 'react-bootstrap-v5';





interface IMainProps {
  loadData: (data: any) => void;
  loadDataa: () => void;
  loadCategory: () => void;
  dashboardData: any;
  getallusers: () => void;
  allusers: any;
}

class Index extends React.Component<IMainProps, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      //   daterange: [
      //     {
      //       startDate: new Date(),
      //       endDate: new Date(),
      //       key: 'selection',
      //     },
      //   ],
      fromDate: null,
      toDate: null,
      visible :false,
    };
  }


  toggleModal = () => {
    alert('hi');
  };
 
  approveModal = () => {
    this.setState({loading: true });
    setTimeout(() => this.setState({ 
      loading: false,
      success: true 
    }), 1000)
    setTimeout(() => this.setState({ 
      isOpen: false 
    }), 3000)
  };

  componentDidMount() {
    this.props.loadData({});
    this.props.getallusers();
    console.log('this.props.dashboardData', this.props.dashboardData);
  }

  doFilter = () => {
    this.props.loadData({
      fromDate: this.state.fromDate,
      toDate: this.state.toDate,
    });
  };
  handleSelect = (item: any) => {
    console.log('item', item);
    console.log('state', this.state.daterange);
    this.setState({ daterange: [item.selection] });
  };
  goback =() =>{
    window.location.href="/dashboard/analytics/tap"
  }
  deleteRow = (param: string) => {
  
    API.delete(PathApi.deleteUser + param)
      .then((data: any) => {
        this.props.loadDataa();
        successToast('Deleted Successfully');
        this.setState({ isLoading: false });
        this.setIdList(param, false, 's');
      })
      .catch((error: any) => {
        this.setState({ isLoading: false });
       
      });
     window.location.href="/dashboard/analytics/users"
  };
  setIdList = (ids: any, isSelected: any, type: string = 's') => {
    if (type === 's') {
      let idArr = this.state.selectedIDs;
      if (!isSelected) {
        idArr = this.state.selectedIDs.filter((e: any) => e.id != ids.id);
      } else {
        idArr.push(ids);
      }
      this.setState({ selectedIDs: idArr }, () => {
        console.log('ids s', this.state.selectedIDs);
      });
    } else {
      this.setState({ selectedIDs: isSelected ? ids : [] }, () => {
        console.log('ids m', this.state.selectedIDs);
      });
    }
  };

  columns: any = [
    {
      formatter: (cell: any, row: any) => <PTag text={row.id} />,
      text: 'ID',
      dataField: 'id',
      sort: true,
      filter: false,
    },
    // {
    //   formatter: (cell: any, row: any) => (
    //     <a href={`https://tapyourchip.com/${row.id}`} target={'_blank'}>
    //       <PTag text={`https://tapyourchip.com/${row.id}`} />
    //     </a>
    //   ),
    //   text: 'Profile Link',
    //   dataField: 'id',
    //   sort: true,
    //   filter: false,
    // },
    {
      formatter: (cell: any, row: any) => <PTag text={row.username} />,
      text: 'Username',
      dataField: 'username',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.email} />,
      text: 'Email',
      dataField: 'email',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.phonenumber} />,
      text: 'Phone Number',
      dataField: 'phonenumber',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.location} />,
      text: 'Location',
      dataField: 'location',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.description} />,
      text: 'Description',
      dataField: 'description',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.dateofbirth} />,
      text: 'Date of birth',
      dataField: 'dateofbirth',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.typeaccount} />,
      text: 'Type Account',
      dataField: 'typeaccount',
      sort: true,
      filter: textFilter(),
    },
    {
      formatter: (cell: any, row: any) => (
        <Col>
          <Link
            to={{
              pathname: `/dashboard/userDetails/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm">
              View
            </Button>
          </Link>
          <Link
            to={{
              pathname: `/dashboard/userDetails/${row.id}/edit`,
            }}
          >
           


            <Button variant="warning" size="sm">
              Edit
            </Button>
          </Link>
          <Link
            to={{
              pathname: `/dashboard/userDetailss/${row.id}/edit`,
            }}
          >
           


            <Button style={{backgroundColor:"Green"}} variant="warning" size="sm">
              Add Image 
            </Button>
          </Link>

          <Link
            to={{
              pathname: `/dashboard/userDeletetest/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm"  style={{backgroundColor:"red"}}>
              Delete
            </Button>
          </Link>











         {/*  <Button
            variant="danger"
            size="sm"
            style={{backgroundColor:"red"}}
            onClick={() => this.deleteRow(row.id)}
          >
            Delete
          </Button> */}
        
     
    
        </Col>
      ),
      text: 'Action',
      dataField: 'id',
    },
    
    
  ];

  //   handleSubmit = (event: any) => {
  //     event.preventDefault();
  //     console.log(event.target.value);
  //   };

  render() {
    // const { dashboardData, } = this.props;

    const { dashboardData } = this.props;
    const { loading, success } = this.state;
    return (
      <>

    
      <Row>
        <Row>
          <CaptionElement title={'List of Users-Metropolitan Security'} />
          <Button
            variant="danger"
            size="sm"
            style={{backgroundColor:"red" , width:"fit-content", margin :"0 auto"}}
            onClick={() => this.goback()}
          >
           Add user
          </Button>
          {/* <DateRangePicker
            onChange={(item) => this.handleSelect(item)}
            ranges={this.state.daterange}
          /> */}
       
          {/*this.props.allusers && this.props.allusers.map((n:any)=><p key={n.id}>{n.username} {n.email}</p>)
*/}



          {dashboardData.userAlanytics && (
            <DataTableList
              data={dashboardData.userAlanytics}
              columns={this.columns}
            />
          )}
        </Row>
      </Row>
      </>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  dashboardData: state.Dashboard,
  allusers: state.Users.payload,
});
const mapDispatchToProps = (dispatch: any) => ({
  loadData: (data: any) => dispatch(fetchUserAnalyticsData(data)),
  getallusers: () =>dispatch(usersget()),
});
export default connect(mapStateToProps, mapDispatchToProps)(Index);
