import React from 'react';
import { connect } from 'react-redux';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { fetchNationalNewsAnalyticsData, fetchUserAnalyticsData } from '../../../../store/actions/dashboard.action';
import { DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import {
  DataTableList,
  PTag,
  textFilter,
  dateFilter,
  CaptionElement,
} from '../../../../components/DataTable';
import { Link } from 'react-router-dom';
import moment from 'moment';
import { addDays } from 'date-fns';
import Input from 'react-select/src/components/Input';
import { nationalnewsget } from '../../../../store/actions/nationalnews.action';
import { successToast } from '../../../../lib/toasthelper';
import { API } from '../../../../config/api.config';
import { PathApi } from '../../../../config/api.path.config';

interface IMainProps {
  loadData: (data: any) => void;
  loadDataa: () => void;
  loadCategory: () => void;
  dashboardData: any;
  getallnationalnews: () => void;
  allnationalnews: any;
}

class Index extends React.Component<IMainProps, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      //   daterange: [
      //     {
      //       startDate: new Date(),
      //       endDate: new Date(),
      //       key: 'selection',
      //     },
      //   ],
      fromDate: null,
      toDate: null,
    };
  }

  componentDidMount() {
    this.props.loadData({});
    this.props.getallnationalnews();
    console.log('this.props.dashboardData', this.props.dashboardData);
 
  }

  doFilter = () => {
    this.props.loadData({
      fromDate: this.state.fromDate,
      toDate: this.state.toDate,
    });
  };
  goback =() =>{
    window.location.href="/dashboard/analytics/national"
  }
  handleSelect = (item: any) => {
    console.log('item', item);
    console.log('state', this.state.daterange);
    this.setState({ daterange: [item.selection] });
  };
  
  deleteRow = (param: string) => {
    API.delete(PathApi.deleteNationalNews + param)
      .then((data: any) => {
        this.props.loadDataa();
        successToast('Deleted Successfully');
        this.setState({ isLoading: false });
        this.setIdList(param, false, 's');
      })
      .catch((error: any) => {
        this.setState({ isLoading: false });
       
      });
     window.location.href="/dashboard/analytics/listnationalnews"
  };
  setIdList = (ids: any, isSelected: any, type: string = 's') => {
    if (type === 's') {
      let idArr = this.state.selectedIDs;
      if (!isSelected) {
        idArr = this.state.selectedIDs.filter((e: any) => e.id != ids.id);
      } else {
        idArr.push(ids);
      }
      this.setState({ selectedIDs: idArr }, () => {
        console.log('ids s', this.state.selectedIDs);
      });
    } else {
      this.setState({ selectedIDs: isSelected ? ids : [] }, () => {
        console.log('ids m', this.state.selectedIDs);
      });
    }
  };

  columns: any = [
    {
      formatter: (cell: any, row: any) => <PTag text={row.id} />,
      text: 'ID',
      dataField: 'id',
      sort: true,
      filter: textFilter(),
     
    },
    // {
    //   formatter: (cell: any, row: any) => (
    //     <a href={`https://tapyourchip.com/${row.id}`} target={'_blank'}>
    //       <PTag text={`https://tapyourchip.com/${row.id}`} />
    //     </a>
    //   ),
    //   text: 'Profile Link',
    //   dataField: 'id', 
    //   sort: true,
    //   filter: false,
    // },
    {
      formatter: (cell: any, row: any) => <PTag text={row.title} />,
      text: 'Title',
      dataField: 'title',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any ) => <PTag text={row.description} />,
      text: 'Description',
      dataField: 'description',
      sort: true,
      filter: textFilter(),
    
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.risklevel} />,
      text: 'Risk Level',
      dataField: 'risklevel',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.longitude} />,
      text: 'Longitude',
      dataField: 'longitude',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => <PTag text={row.latitude} />,
      text: 'Latitude',
      dataField: 'latitude',
      sort: true,
      filter: textFilter(),
     
    },
    {
      formatter: (cell: any, row: any) => (
        <Col>
          <Link
            to={{
              pathname: `/dashboard/nationalnewsDetails/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm">
              View
            </Button>
          </Link>
          <Link
            to={{
              pathname: `/dashboard/nationalnewsDetails/${row.id}/edit`,
            }}
          >
            <Button variant="warning" size="sm">
              Edit
            </Button>
          </Link>
          <Link
            to={{
              pathname: `/dashboard/nationalnewsDeletetest/${row.id}`,
            }}
          >
            <Button variant="primary" size="sm" style={{backgroundColor:"red"}} >
              Delete
            </Button>
          </Link>
        </Col>
      ),
      text: 'Action',
      dataField: 'id',
    },
   
    
    
    
  ];

  //   handleSubmit = (event: any) => {
  //     event.preventDefault();
  //     console.log(event.target.value);
  //   };

  render() {
    // const { dashboardData, } = this.props;

    const { dashboardData } = this.props;

    return (
      <Row>
        <Row>
          <CaptionElement title={'List of National News Risk'} />
          <Button
            variant="danger"
            size="sm"
            style={{backgroundColor:"red", width:"fit-content", margin :"0 auto"}}
            onClick={() => this.goback()}
          >
           Add National Risk News
          </Button>
          {/* <DateRangePicker
            onChange={(item) => this.handleSelect(item)}
            ranges={this.state.daterange}
          /> */}
  
          {dashboardData.userAlanytics && (
            <DataTableList
              data={dashboardData.userAlanytics}
              columns={this.columns}
            />
          )}

                     
<div>
          {//this.props.allnationalnews && this.props.allnationalnews.map((l:any)=><p key={l.id}>{l.title} {l.description}</p>)
}


</div>

        </Row>
      </Row>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  dashboardData: state.Dashboard,
  allnationalnews: state.Nationalnews.payload,
});
const mapDispatchToProps = (dispatch: any) => ({
  loadData: (data: any) => dispatch(fetchNationalNewsAnalyticsData(data)),
  getallnationalnews: () =>dispatch(nationalnewsget()),
});
export default connect(mapStateToProps, mapDispatchToProps)(Index);
